import { createElement } from '../../util';
import aria from '../../aria-attributes';

const className = 'link-finder';
const fileTypes = ['pdf', 'xls', 'doc', 'docx', 'txt'];

function findLinks(anchorsArea) {
    const anchors = anchorsArea.querySelectorAll('a');

    var promise = new Promise(function(resolve) {
        resolve(findDocumentLinks(anchors));
    });

    promise.then(function() {
        findExternalLink(anchors);
    });
}

function findDocumentLinks(anchors) {
    anchors.forEach(element => {
        let downloadAttri = element.getAttribute('download');

        if (downloadAttri || downloadAttri == '') {
            createElements(element, 'download');
        } else {
            for (let i in fileTypes) {
                let regex = new RegExp('.' + fileTypes[i]);

                if (regex.test(element.href)) {
                    createElements(element, fileTypes[i]);
                }
            }
        }
    });
}

function findExternalLink(anchors) {
    anchors.forEach(function(anchor) {
        /** checks if anchors links are :
         * external
         * not an image
         * not contain font awesome external link icon already
         * fab for social icons
         * is not a social icon
         * not a CTA
         * not an email hyperlink
         * not a telephone number link
         * has to contain a href value
         */
        if (
            anchor.origin !== window.location.origin &&
            anchor.querySelectorAll('img').length < 1 &&
            anchor.querySelectorAll('.fa-external-link').length < 1 &&
            anchor.querySelectorAll('.fab').length < 1 &&
            anchor.className !== 'social-icon' &&
            !anchor.parentElement.className.includes('cta-block') &&
            anchor.href.indexOf('mailto:') !== 0 &&
            anchor.href.indexOf('tel:') !== 0 &&
            anchor.origin
        ) {
            // adds font awesome external link icon after completing checks

            let node = document.createElement('span');
            node.className = 'far fa-external-link inline-external-link';
            node.setAttribute(aria.label, '(external link)');
            anchor.appendChild(node);
        }
    });
}

function createElements(anchor, type) {
    if (type !== 'download' && type !== 'pdf') {
        type = 'download';
    }

    let elFA = createElement(
        'span',
        '',
        '',
        '',
        'fas',
        'fa-file-' + type,
        'document-download__icon',
        '',
        '',
        'aria-hidden',
        'true'
    );
    let elSR = createElement('span', 'Download ', '', '', 'sr-only');

    anchor.parentNode.prepend(elFA);
    anchor.prepend(elSR);
}

export default {
    launchFn: findLinks,
    launchQuery: `.${className}`,
};
